import city from '@/common/city.js'
import citySelect from '@/components/uni-city-select/uni-city-select.vue'
import tuijianContentList from '@/components/dzy-tuijian-content-list/dzy-tuijian-content-list.vue'
export default {
	components:{
		citySelect,
		tuijianContentList
	},
	//1.初始化模板变量
	data() {
		return {
			//获取自定义$commonConfig对象中的服务器地址
			serverImgUrl:this.$commonConfig.serverImgUrl,
			serverApiUrl:this.$commonConfig.serverApiUrl,
			//推荐内容
			tuijianContent:[],
			//获取定位城市处->上下图标切换
			downUpImg:"xiala-down.png",
			
			//获取定位城市
			cityName:'上海',//默认定位城市
			
			//城市选择面板
			isActive:false,
			listData: city,
			quickPanelData:[
				{
				title:'当前城市',
				navName: '当前',
				data:['上海'], //默认显示当前城市
				height: 150
			},
			{
				title:'热门城市',
				navName: '热',
				data:['上海','北京','成都','昆明','西安'],
				height: 224
			}
			]
		}
	},
	
	//2.页面加载完成、页面卸载事件
	onLoad() {
		//执行uni-app提供的类似ajax异步加载
		uni.request({ 
			url: this.serverApiUrl+'home/house/kuai_hot', //请求url
			method: 'POST',               //请求方式 
			data: {},                     //传递的数据
			success: res => {   //成功执行回调函数
				if(res.statusCode==200){
					// console.log(res.data);
					this.tuijianContent= res.data;
				}else{ 
					// console.log(res);
				}
				
			},
			fail: () => {},
			complete: () => {}
		});
	}, 
	
	//3.事件以及自定义方法存放处
	methods: {
		showCitySelect(){
			this.downUpImg="xiala-up.png";//切换上下图标
			this.isActive=true;
		},
		hideCitySelect(){
			this.downUpImg="xiala-down.png"; //切换上下图标
			this.isActive=false;
		},
		chooseItem(item) {
			this.downUpImg="xiala-down.png"; //切换上下图标
			this.isActive=false; //隐藏城市面板
			this.cityName=item; //赋值给当前定位城市
			this.quickPanelData[0].data=[item]; //赋值给面板当前城市
		}
	}, 

}
